import { log } from "util";
import screenfull from "screenfull"
import { _get,_delete,addRoleMsg,getRoleMsg,editRoleMsg,switchRoleMsg,getRolePageMsg} from "../../../api/index.js"
export default {
  name: "AdminUser",
  data() {
    return {
      //新增角色信息
      roleName:"",
      roleStatus:true,
      //编辑信息
      editName:"",
      editStatus:"",
      id:"",
      //分页参数
      currentPage:"",
      total:"",
      pageSize:10,
      // 管理员信息数据
      tableData:[],
      // 信息编辑
      outerVisible: false,
      innerVisible: false,
      // 搜索信息
      value9: "",
      input10: "",
      // 添加信息
      dialogFormVisible: false,
      form: {
        name: ""
      },
      formLabelWidth: "100px",
      // 性别下拉框
      sexOptions: [
        {
          value: "1",
          label: "男"
        },
        {
          value: "0",
          label: "女"
        }
      ],
      sexValue: "男",
      // 状态下拉框
      stateOptions: [
        {
          value: "1",
          label: "启用"
        },
        {
          value: "0",
          label: "禁用"
        },
        {
          value: "2",
          label: "全部"
        }
      ],
      stateValue: "全部",
      addValue: true,
      editValue:true
    };
  },
  created(){
      this.getTableData();
  },
  methods: {
    getTableData(){
      getRoleMsg().then(res => {
              let data=res.data.data.data;
              let paramsData=res.data.data;
              this.currentPage=paramsData.current_page;
              this.total=paramsData.total;
              this.pageSize=paramsData.per_page;
             if(data.length>0){
                let tableList=[];
                for(let i=0;i<data.length;i++){
                   tableList.push({
                    id:data[i].id,
                    name:data[i].name,
                    status:data[i].status?true:false,
                    permissions:data[i].permissions,
                    is_manager:data[i].is_manager,
                   })
                 } 
               this.tableData=tableList;
            }
       });
    },
    formatter(row, column) {
      return row.address;
    },
    // 分配权限
    handleRole(index, row) {
       this.$router.push({path:'/assignRoleList',query:{id:row.id,permissions:row.permissions,name:row.name,status:row.status?1:0,is_manager:row.is_manager?1:0}});
    },
    //修改角色列表状态
    handleStatus(index,row){
      _get("api/role/toggle/"+row.id).then(res => {
         this.getTableData();
         this.$message({
              message: "状态修改成功",
              type: "success"
            });
       })
    },
    // 编辑角色信息
    handleEdit(index, row) {
      this.editName=row.name;
      if(row.is_manager){
        this.editStatus=true
      }else{
        this.editStatus=false
      }
      this.id=row.id;
      // 编辑信息
      this.outerVisible = true;
    },
    // 删除管理员信息
    handleDelete(index, row) {
      this.$confirm(`确定删除 ${row.name} 吗?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
           _delete("api/role/"+row.id).then(res=>{
            this.getTableData();
            this.$message({
              message: "删除成功",
              type: "success"
            });
          }).catch(error=>{
            this.$message({
              message: "删除失败",
              type: "error"
            });
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    isUpdate() {
      let params={id:this.id,name:this.editName,is_manager:this.editStatus?1:0}
      this.$confirm("确定修改吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          editRoleMsg(params).then(res=>{
            this.$message({
              message: "编辑成功",
              type: "success"
            });
          }).catch(error=>{
            this.$message({
              message: "编辑失败",
              type: "error"
            });
          });
          this.outerVisible = false;
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消修改"
          });
          this.outerVisible = false;
        });
    },
    // 分页
    handleSizeChange(val) {
      //console.log(`每页 ${val} 条`);
      let params={per_page:val};
      _get("api/role/",params).then(res => {
              let data=res.data.data.data;
              let paramsData=res.data.data;
              this.currentPage=paramsData.current_page;
              this.total=paramsData.total;
              this.pageSize=paramsData.per_page;
              if(data.length>0){
                let tableList=[];
                for(let i=0;i<data.length;i++){
                   tableList.push({
                    id:data[i].id,
                    name:data[i].name,
                    status:data[i].status,
                    permissions:data[i].permissions,
                    is_manager:data[i].is_manager?true:false,
                   })
                 } 
               this.tableData=tableList;
              }
       });
    },
    handleCurrentChange(val) {
      let params={page:val};
      getRolePageMsg(params).then(res => {
              let data=res.data.data.data;
              let paramsData=res.data.data;
              this.currentPage=paramsData.current_page;
              this.total=paramsData.total;
              this.pageSize=paramsData.per_page;
              if(data.length>0){
                let tableList=[];
                for(let i=0;i<data.length;i++){
                   tableList.push({
                    id:data[i].id,
                    name:data[i].name,
                    status:data[i].status,
                    permissions:data[i].permissions,
                    is_manager:data[i].is_manager?true:false,
                   })
                 } 
               this.tableData=tableList;
              }
       });
    },
    // 添加信息
    addAdminUser() {
      this.dialogFormVisible = true;
    },
    // 确定添加
    isAddUser() {
      let status;
      if(this.roleStatus){
        status=1;
      }else{
        status=0;
      }
      let params={name:this.roleName,is_manager:status}
      addRoleMsg(params).then(res=>{
        this.dialogFormVisible = false;
        this.$message({
          message: "添加成功",
          type: "success"
        });
      }).catch(error=>{
        this.$message({
          message: "添加失败",
          type: "error"
        });
      });
 
    }
  },
  watch:{
    
  }
};